<template>
  <div class="page cases">
  	<section class="section">
	  	<div class="container">
        <div class="cases__headline">
          <h1 class="title cases-title">
            А это чудеса. Они случаются.
          </h1>
          <p class="subtitle cases-subtitle">
            Каждый проект - реузльтат, измеряемый в&nbsp;цифрах
          </p>
        </div>

    	</div>
  	</section>

  </div>

</template>

<script>
  import {mapGetters} from 'vuex';
  import {mapActions} from 'vuex';

  export default {
    data () {
      return {
        theme: 'white-theme'
      }
    },
    computed: {
      ...mapGetters('page', {
        thisTheme: 'theme'
      }),
    },
    methods: {
      ...mapActions('page', {
        changeTheme: 'changeTheme'
      })
    },
    beforeMount() {
      this.changeTheme(this.theme)
    },
    mounted: function () {
      document.querySelector('.header').classList.remove('fadeMenu');
    }
  }

</script>

<style lang="scss" scoped>

	 @import '../style/blocks/_cases.scss';

</style>



